
import api from '@/services/api';
import Swal from 'sweetalert2';
import { Options, Vue } from 'vue-class-component';
import { mapActions } from 'vuex';
import BaseSpinner from './UI/BaseSpinner.vue';

interface Location {
  woeid: number;
  name: string;
}

@Options({
  components: { BaseSpinner },
  props: ['show'],
  emits: ['on-close'],
  methods: {
    ...mapActions(['getWeatherFromWoeid']),
  },
})
export default class extends Vue {
  show!: boolean;

  getWeatherFromWoeid!: (woeid: number) => void;

  isInputFocused = false;

  searchQuery = '';

  locations: Location[] = [];

  isLoading = false;

  handleClose(e: Event) {
    this.$emit('on-close', e);
  }

  handleInputFocus() {
    this.isInputFocused = true;
  }

  handleInputBlur() {
    this.isInputFocused = false;
  }

  async getLocations() {
    if (this.searchQuery) {
      this.isLoading = true;
      try {
        const { data } = await api.get('/location/search', {
          params: {
            query: this.searchQuery,
          },
        });

        this.locations = data.slice(0, 5).map((location: any) => ({
          woeid: location.woeid,
          name: location.title,
        }));
      } catch (error) {
        Swal.fire({
          title: 'Error!',
          icon: 'error',
          text: `Error while searching locations: ${error.message}`,
        });
      } finally {
        this.isLoading = false;
      }
    }
  }

  getNewData(woeid: number, event: Event) {
    this.getWeatherFromWoeid(woeid);
    this.handleClose(event);
  }
}
